// Esse reducer ser responsvel por tratar as informaes da pessoa usuria
const INITIAL_STATE = {
  email: '',
};

const SAVE_INFO_USER = 'SAVE_INFO_USER';

const user = (state = INITIAL_STATE, action) => {
  switch (action.type) {
  case SAVE_INFO_USER:
    return {
      ...state,
      email: action.email,
    };
  default:
    return state;
  }
};

export default user;
